import React from 'react';

import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Divider from '@mui/material/Divider';
import Typography from '@mui/material/Typography';
import { SelectOption } from '@mui/base';
import { styled } from '@mui/material/styles';

import MHFormControl from '../Form/MHFormControl';
import MHButton from '../Button/MHButton';
import InputAdornment from '../Form/InputAdornment';
import useInput from '../../hooks/use-input';

import * as validators from '../../utils/validators';
import OnboardingContext, {
  EmployerOnboarding
} from '../../store/context/onboarding-context';
import {
  addDaysToDate,
  constructBillingDateFormat,
  formatAmount,
  formatNumber,
  parseAmount,
  resolveErrorMessage
} from '../../utils/utils';
import AllocationSummary from '../Wallet/AllocationSummary';
import BillingPeriod from '../Wallet/BillingPeriod';
import InvoiceDialog from '../Wallet/InvoiceDialog';
import MHRadioGroup from '../Form/MHRadioGroup';
import StackedContainer from '../Wallet/StackedContainerStyled';
import AllocationPerPeriod from '../Wallet/AllocationPerPeriod';
import Label from '../Form/Label';
import EmployeeStat, { EmployeeStatProps } from '../Wallet/EmployeeStat';
import RoundedLogoIcon from '../../theme/icons/RoundedLogo';
import { ReactComponent as UsersGroupIcon } from '../../static/svg/users-group.svg';
import { ReactComponent as ArrowLeftIcon } from '../../static/svg/arrow-left.svg';
import { ReactComponent as DollarIcon } from '../../static/svg/dollar.svg';
import { ALLOCATION_FIELDS } from '../../utils/constants';
import useInputArray from '../../hooks/use-input-array';
import { Category } from '../../models/wallet.model';

const SelectTag = styled('span')(
  ({ theme }) => `
  border-radius: 3px;
  background: #EBE5F1;
  padding: 4px 9px;
  display: inline-block;
  color: ${theme.palette.primary.main};
`
);

const GridItem = styled(Box)(({ theme }) => ({
  // ...theme.typography.body2,
  backgroundColor: '#ffffff',
  padding: theme.spacing(3),
  color: theme.palette.text.secondary,
  // flex: '1 1 auto',
  display: 'flex',
  justifyContent: 'center'
}));

type AllocationPeriod = 'monthly' | 'quarterly';

const PERIOD_OPTIONS = [
  {
    value: 'monthly',
    label: 'Monthly'
  },
  {
    value: 'quarterly',
    label: 'Quarterly'
  }
];

const FinalStep = (props: {
  activeIndex: number;
  onNext: () => void;
  onPrevious: (e: React.MouseEvent) => void;
}) => {
  const onboardingCtx = React.useContext(OnboardingContext);
  const { employer, updateEmployerData, categoriesMap } = onboardingCtx;

  const { activeIndex, onNext, onPrevious } = props;

  const [monthlyAllocation, setMonthlyAllocation] = React.useState(0);
  const [quarterlyAllocation, setQuarterlyAllocation] = React.useState(0);

  const EMPLOYEE_STAT: EmployeeStatProps = {
    theme: 'dark',
    icon: (
      <RoundedLogoIcon sx={{ p: 0.6, mb: 2 }}>
        <UsersGroupIcon />
      </RoundedLogoIcon>
    ),
    title: 'No of Employees',
    stat: employer?.employeeSize as number
  };

  const {
    value: enteredAllocation,
    valid: enteredAllocationIsValid,
    error: enteredAllocationHasError,
    onChange: allocationInputChangeHandler,
    onBlur: allocationInputBlurHandler,
    markAsTouched: markAllocationInputAsTouched
  } = useInput([
    {
      validator: (value: string) => validators.required(value)
    }
  ]);

  const { inputFields, onChange, addField, removeField } = useInputArray([
    {
      validator: (value: string) => validators.required(value)
    }
  ]);

  const [allocationDuration, setAllocationDuration] = React.useState<
    AllocationPeriod
  >('quarterly');

  const [allocationBuckets, setAllocationBuckets] = React.useState<
    readonly Category[]
  >(categoriesMap.get('categories') ? categoriesMap.get('categories')! : []);

  const allocationDurationChangeHandler = (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    setAllocationDuration(e.target.value as AllocationPeriod);
  };

  const SUB_ALLOCATION_BUCKETS = allocationBuckets.filter(
    (category) => !isCategoryAnythingBucket(category)
  );

  React.useEffect(() => {
    for (const bucket of SUB_ALLOCATION_BUCKETS) {
      addField();
    }
  }, []);

  React.useEffect(() => {
    window.scrollTo(0, 0);
    if (!employer) {
      return;
    }

    const emAllocation = employer.allocationPerEmployee
      ? employer.allocationPerEmployee.toString()
      : '';

    allocationInputChangeHandler(emAllocation);

    if (employer?.allocationBuckets) {
      SUB_ALLOCATION_BUCKETS.forEach((bucket, index) => {
        const prevBucket = employer.allocationBuckets.find(
          (buck) => buck.id === bucket.id
        );
        if (!prevBucket) {
          return;
        }
        onChange(formatNumber(prevBucket.allocation, 2), index);
      });
    }
  }, []);

  React.useEffect(() => {
    if (inputFields.length) {
      computeAllocation();
    }
  }, [inputFields]);

  function isCategoryAnythingBucket(category: Category): boolean {
    if (!category || !category.categoryName) {
      return false;
    } else if (category.categoryName.toLowerCase().trim() === 'anything') {
      return true;
    }
    return false;
  }

  // const computeAllocation = (e: React.ChangeEvent<HTMLInputElement>) => {
  //   allocationInputChangeHandler(e);
  //   // computeAllocation(+parseAmount(e.target.value));
  // };

  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const computeAllocation = () => {
    const employeePopulation =
      employer && employer?.employeeSize ? employer.employeeSize : 0;

    console.log(inputFields);

    setAllocationBuckets((buckets) => {
      const modifiedSubBuckets = SUB_ALLOCATION_BUCKETS.map((bucket, i) => ({
        ...bucket,
        allocation: inputFields.length ? +parseAmount(inputFields[i].value) : 0
      }));

      let [anythingBucket] = buckets.filter((bucket) =>
        isCategoryAnythingBucket(bucket)
      );
      anythingBucket.allocation = +parseAmount(enteredAllocation);

      return [...[anythingBucket], ...modifiedSubBuckets];
    });

    const totalAmount =
      inputFields.reduce(
        (prev, current, index) => prev + +parseAmount(current.value),
        0
      ) + +parseAmount(enteredAllocation);

    const allocationPerMonth = employeePopulation * totalAmount;
    setMonthlyAllocation(allocationPerMonth);
    setQuarterlyAllocation(allocationPerMonth * 3);
  };

  const patchEmployerData = () => {
    updateEmployerData({
      ...(employer as EmployerOnboarding),
      allocationPerEmployee: +parseAmount(enteredAllocation),
      monthlyAllocation: monthlyAllocation,
      quarterlyAllocation: quarterlyAllocation,
      allocationBuckets
    });
  };

  function submitHandler(e: React.SyntheticEvent<HTMLFormElement>) {
    e.preventDefault();

    if (!enteredAllocationIsValid) {
      markAllocationInputAsTouched();
      return;
    }

    handleOpen();
    patchEmployerData();
    // updateEmployerData({
    //   allocationPerEmployee: +parseAmount(enteredAllocation),
    //   monthlyAllocation: monthlyAllocation,
    //   quarterlyAllocation: quarterlyAllocation,
    //   allocationBuckets
    // } as EmployerOnboarding);

    // console.warn(employer);
    onNext();
  }

  function previousStepHandler(e: React.MouseEvent) {
    e.preventDefault();

    patchEmployerData();
    onPrevious(e);
  }

  return (
    <React.Fragment>
      <MHButton
        variant="text"
        startIcon={<ArrowLeftIcon color="#194049" />}
        sx={{
          mb: 3
        }}
        onClick={previousStepHandler}>
        Back to Employer Details
      </MHButton>

      <Grid container spacing={4}>
        <Grid item xs={5}>
          <Grid container spacing={2} mb={4}>
            <Grid item xs={8}>
              <EmployeeStat
                {...EMPLOYEE_STAT}
                containerSx={{
                  py: 2,
                  minHeight: 70,
                  width: '100%'
                }}
              />
            </Grid>
          </Grid>

          <Paper
            sx={{
              py: 4,
              px: 3,
              borderRadius: 4
            }}>
            <Box component="form" id="allocation-form" onSubmit={submitHandler}>
              <MHFormControl
                id="allocation"
                type="number"
                label={'Monthly stipend Per Employee for Anything ($)'}
                placeholder="Amount ($)"
                value={enteredAllocation}
                onChange={allocationInputChangeHandler}
                onBlur={() => {
                  allocationInputBlurHandler();
                  computeAllocation();
                }}
                precision={2}
                startAdornment={
                  <InputAdornment>
                    <DollarIcon width="1rem" />
                  </InputAdornment>
                }
                autoComplete="off"
                error={resolveErrorMessage(enteredAllocationHasError)(
                  'Please enter your allocation per employee'
                )}
              />

              {inputFields.map((field, i) => (
                <Stack
                  key={i}
                  direction="row"
                  alignItems="center"
                  //   justifyContent="space-between"
                  sx={{
                    '& > div, & > label': {
                      flexGrow: 1
                    }
                  }}>
                  <Label
                    sx={{
                      width: '50%',
                      minWidth: 0
                    }}>
                    {SUB_ALLOCATION_BUCKETS[i] &&
                      SUB_ALLOCATION_BUCKETS[i].categoryName}
                  </Label>
                  <MHFormControl
                    id={'allocation-bucket' + i}
                    type="number"
                    placeholder="Amount ($)"
                    value={field.value}
                    onChange={(e) => {
                      onChange(e, i);
                    }}
                    onBlur={computeAllocation.bind(null)}
                    precision={2}
                    startAdornment={
                      <InputAdornment>
                        <DollarIcon width="1rem" />
                      </InputAdornment>
                    }
                    autoComplete="off"
                  />
                </Stack>
              ))}
            </Box>
          </Paper>
        </Grid>

        <Grid item xs={7}>
          <AllocationPerPeriod
            monthlyAllocation={monthlyAllocation}
            quarterlyAllocation={quarterlyAllocation}
          />

          <StackedContainer
            direction="column"
            alignItems="flex-start"
            py={1.5}
            px={3}
            mb={2}>
            <Typography variant="subtitle2" fontSize=".8rem">
              Payment Duration
            </Typography>

            <MHRadioGroup
              row
              name="allocation-period"
              id="allocation-period"
              options={PERIOD_OPTIONS}
              value={allocationDuration}
              onChange={allocationDurationChangeHandler}
              color="default"
              controlSx={{
                mb: 0
              }}
            />
          </StackedContainer>

          <AllocationSummary allocationFields={allocationBuckets} />

          <BillingPeriod />

          <MHButton
            type="submit"
            form="allocation-form"
            sx={{
              mt: 2
            }}
            fullWidth>
            Generate Invoice
          </MHButton>
        </Grid>
      </Grid>

      {/* <InvoiceDialog open={open} onClose={handleClose} /> */}
    </React.Fragment>
  );
};

export default FinalStep;
