import React from 'react';

import { SelectOption } from '@mui/base';
import { Category } from '../../models/wallet.model';
import useHttp from '../../hooks/use-http';
import { HttpResponse } from '../../models/api.interface';

export type EmployerOnboarding = {
  customerId: number;
  employerRefId: number;
  employerToken: string;
  base64Logo: string;
  companyName: string;
  domain: string;
  employeeEmail: string;
  firstName: string;
  lastName: string;
  employeeSize: number;
  stateOfIncorporation: string;
  allocationPerEmployee: number;
  monthlyAllocation: number;
  quarterlyAllocation: number;
  businessPhone: string;
  businessAddress: string;
  businessType: string;
  region: string;
  city: string;
  zipCode: string;
  state: string;
  allocationBuckets: readonly Category[];
};

type OnboardingCtxShape = {
  states: SelectOption<string>[];
  employer: EmployerOnboarding | null;
  updateEmployerData: (emp: EmployerOnboarding) => void;
  configureStates: (geoData: any) => void;
  categoriesMap: Map<string, Category[]>;
};

const OnboardingContext = React.createContext<OnboardingCtxShape>({
  states: [],
  employer: null,
  updateEmployerData: (emp: EmployerOnboarding) => {},
  configureStates: (geoData: any) => {},
  categoriesMap: new Map<string, Category[]>()
});

export const OnboardingContextProvider = ({
  children
}: {
  children: React.ReactNode;
}) => {
  const [employer, setEmployer] = React.useState<EmployerOnboarding | null>(
    null
  );
  const [states, setStates] = React.useState<Array<SelectOption<string>>>([]);

  const [categoryCacheMap, setCategoryCacheMap] = React.useState<
    Map<string, Category[]>
  >(new Map());

  const { sendHttpRequest: fetchCategories } = useHttp();

  React.useEffect(() => {
    fetchCategories(
      process.env.REACT_APP_API_BASE_URL + 'employee/dashboard/category/all',
      {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          // 'Authorization': `Bearer ${authCtx.token}`
        }
      },
      (response: HttpResponse<Category[]>) => {
        setCategoryData(response.data);
      }
    );
  }, []);

  const setCategoryData = (categories: Category[]) => {
    setCategoryCacheMap((prevState) => {
      const newState = new Map(prevState);

      if (!categories) {
        return prevState;
      }

      const mappedCategories: (Category &
        SelectOption<string>)[] = categories.map((category) => ({
        ...category,
        value: category.id + '',
        label: category.categoryName
      }));
      newState.set('categories', mappedCategories);
      return newState;
    });
  };

  const configureStates = (geoData: Array<unknown>) => {
    const mappedStates = geoData
      .filter((item: any) => item.fields.ste_type === 'state')
      .map((item: any) => {
        return {
          value: item.fields.ste_name,
          label: item.fields.ste_name
        };
      })
      .sort((a, b) => {
        if (a.label < b.label) {
          return -1;
        }
        if (a.label > b.label) {
          return 1;
        }
        return 0;
      });
    setStates(mappedStates);
  };

  const updateEmployerData = (empDetails: EmployerOnboarding) => {
    setEmployer((prevState) => ({
      ...prevState,
      ...empDetails
    }));
  };

  const contextValue: OnboardingCtxShape = {
    states,
    employer: employer,
    updateEmployerData,
    configureStates,
    categoriesMap: categoryCacheMap
  };

  return (
    <OnboardingContext.Provider value={contextValue}>
      {children}
    </OnboardingContext.Provider>
  );
};

export default OnboardingContext;
