import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import EmployerOnboarding from '../pages/EmployerOnboarding/EmployerOnboarding';
import DwollaPaymentSuccess from '../pages/EmployerOnboarding/DwollaPaymentSuccess';
import ConsultantOnboarding from '../pages/Consultant/ConsultantOnboarding';
import NotFound from '../pages/Error/404';

import { FnComponent } from '../models/component.model';
import { OnboardingContextProvider } from '../store/context/onboarding-context';

const AppNavigator: FnComponent<{}> = (props) => {
  return (
    <React.Fragment>
      <Switch>
        <Route
          path="/"
          render={(routeProps) => (
            <Redirect
              to={{
                pathname: '/onboarding',
                state: { from: routeProps.location },
                search:
                  'token=eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhdXRob3JpemVkIjp0cnVlLCJlbWFpbCI6Im1pY2hhZWxAbWh3b3JrbGlmZS5jb20iLCJlbXBsb3llclJlZklkIjoxMTY0MDc3Nzg3LCJleHAiOiIyMDIzLTA0LTExVDIwOjIwOjE4LjM2NjY4MDEzOVoiLCJyb2xlIjoiRU1QTE9ZRVJfQURNSU4iLCJ0eXBlIjoiRU1QTE9ZRVJEQVNIQk9BUkQiLCJ1c2VyIjoxMTY0MDU1NTE5fQ.EUN0EBi0FPyRkcB-oiJLfX-4c6uhoWA-ISIj9Pxod6o'
              }}
            />
          )}
          exact
        />

        {/* <Route path={`/onboarding`} component={Onboarding} exact /> */}
        <Route path={`/onboarding`} exact>
          <OnboardingContextProvider>
            <EmployerOnboarding />
          </OnboardingContextProvider>
        </Route>

        <Route path={`/employer/allocation`} exact>
          <DwollaPaymentSuccess />
        </Route>

        <Route path={'/consultant/onboarding'} exact>
          <ConsultantOnboarding />
        </Route>

        <Route path="*">
          <NotFound />
        </Route>

        {/* <Route path="*">
          <Redirect to="/404" />
        </Route> */}
      </Switch>
    </React.Fragment>
  );
};

export default AppNavigator;
